*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  top: auto;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --font-josefin-sans: "Josefin Sans", sans-serif;
  --font-weight-default: 400;
  --font-weight-light: 300;
  --font-weight-bold: 600;
}

body {
  font-family: var(--font-josefin-sans);
  font-weight: var(--font-weight-default);
  color: var(--main);
}

h1,
p {
  margin-top: 0;
}

h1 {
  margin-bottom: 1.25rem;
  line-height: 1.1;
  font-weight: var(--font-weight-bold);
}

p {
  margin-bottom: 1.875rem;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main: hsl(0, 36%, 70%);
  --accent: hsl(0, 93%, 68%);
  --black: hsl(0, 6%, 24%);
  --gradient-main: linear-gradient(135deg, hsl(0, 80%, 86%), hsl(0, 74%, 74%));
  --gradient-grayish: linear-gradient(
    135deg,
    hsl(0, 0%, 100%),
    hsl(0, 0%, 1%)
  );
}

.container {
  padding: 0 1.5625rem;
}

.main {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 71.875em) {
  .main {
    flex-direction: row;
  }
}

.header {
  margin: 1.5rem 0;
}

.header__logo {
  height: 1.125rem;
}

.coming {
  margin: 4.375rem 0;
  text-align: center;
}

.coming__title {
  text-transform: uppercase;
  color: var(--black);
  letter-spacing: 0.625rem;
}
.coming__title span {
  font-weight: var(--font-weight-light);
  color: var(--main);
}

.coming__img {
  width: 100%;
  height: 17.5rem;
  overflow: hidden;
}
.coming__img img {
  width: 100%;
  margin-top: -2.625rem;
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 * 3. Show the overflow in Edge.
 * 4. Remove the inheritance of text transform in Firefox.
 */
button {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
  overflow: visible; /* 3 */
  text-transform: none; /* 4 */
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button {
  cursor: pointer;
  border: none;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  margin-top: 1.25rem;
}
.footer .attribution {
  margin-bottom: 0.375rem;
  font-size: 0.6875rem;
  text-align: center;
  color: var(--black);
}
.footer .attribution a {
  color: var(--accent);
}